import React, { PropTypes } from 'react';
import { Slots, Slot } from './styledComponents';

const fabrics = [
  { name: 'dentelle', slug: 'lace' },
  { name: 'cuir', slug: 'leather' },
  { name: 'soie/mousseline', slug: 'silk' },
  { name: 'tailleur', slug: 'suit' },
  { name: 'maille', slug: 'stitch' },
];

const FabricSelector = ({ selectedFabrics, toggleFabric, selectNone }) => (
  <Slots>
    {fabrics.map(fabric =>
      <Slot
        isSelected={selectedFabrics.includes(fabric.slug)}
        onClick={() => toggleFabric(fabric.slug)}
        key={fabric.slug}
      >{fabric.name}</Slot>,
    )}
    <Slot
      isSelected={selectedFabrics.length === 0}
      onClick={() => selectNone()}
    >aucun</Slot>
  </Slots>
);

FabricSelector.propTypes = {
  selectedFabrics: PropTypes.arrayOf(PropTypes.string).isRequired,
  toggleFabric: PropTypes.func.isRequired,
  selectNone: PropTypes.func.isRequired,
};

export default FabricSelector;



