import React, { PropTypes } from 'react';
import { Link } from 'react-router';
import styled from 'styled-components';
import FabricSelector from './FabricSelector';
import { Wrapper, Block, Title2, Title3, Button3, Logo, Question, Center, Header2, colors, mobileThresholdPixels }
  from './styledComponents';
import LogoImg from '../assets/logo1.png';
import ProgressBar from './order/ProgressBar';
import { trackEvent } from '../services/analytics';

const P = styled.p`
  color: ${colors.navy};
  text-align: justify;

  @media (max-width: ${mobileThresholdPixels}) {
    text-align: left;
    font-size: 11px;
  }
`;

const Step3 = ({ selectedFabrics, toggleFabric, selectNone }) => (
  <Wrapper>
    <Block>
      <Header2>
        <Link
          to={'/'} style={{ flex: 1, textAlign: 'center' }}
          onClick={() => trackEvent('click', 'home_step3')}
        >
          <Logo src={LogoImg} />
        </Link>
        <ProgressBar step={2} />
        <Title2>
          Je fais mon estimation
        </Title2>
        <Title3>
          Mes retouches
        </Title3>
      </Header2>
      <Question>Afin de vous attribuer le couturier le plus adapté à vos besoins, merci de préciser si votre commande
        comporte :</Question>
      <FabricSelector selectedFabrics={selectedFabrics} toggleFabric={toggleFabric} selectNone={selectNone} />
      <P>{`
        Nos experts possèdent les basiques pour vos retouches (boutons, élastiques, doublures, zips, thermocollants)
        dans les coloris classiques. Pour toutes les demandes avec une forme ou un coloris particulier, merci de vous
        les fournir avant votre RDV avec le couturier. Vos questions à l'adresse:
      `}<u>contact@tilli.fr</u></P>
      <Center><Link to={'/order/step4'}><Button3>{'> suivant <'}</Button3></Link></Center>
    </Block>
  </Wrapper>
);

Step3.propTypes = {
  selectedFabrics: PropTypes.arrayOf(PropTypes.string).isRequired,
  toggleFabric: PropTypes.func.isRequired,
  selectNone: PropTypes.func.isRequired,
};

export default Step3;



